body {
    background-color: #00adef;
    margin: 10em;
    font-family: Arial;
}

.background {
    position:absolute;
    left:0;
    top:0;
    margin: 0 auto;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    z-index:10;
}

.movieTitle {
    font-size: 2em;
    padding: 0.5em 0 0.5em 0;
}

details {
    border: 1px solid #aaa;
    border-radius: 4px;
    padding: .5em .5em 0;
    margin-bottom: .8em;
}

.movieImg {
    border: 1px solid #aaa;
    border-radius: 4px;
    padding: .5em .5em .5em;
}

summary {
    text-align: center;
    font-weight: bold;
    margin: -.5em -.25em 0;
    padding: .5em;
}

details[open] {
    padding: .5em;
}

details[open] summary {
    border-bottom: 1px solid #aaa;
    margin-bottom: .5em;
}

ul{
    list-style:none;
    padding-left:0px;
}